<template>
  <!--  查看示例弹框  -->
  <el-dialog
    :model-value="visible"
    class="custom-dialog"
    width="850px"
    lock-scroll
    :close-on-click-modal="false"
    append-to-body
    title="操作记录"
    @open="dialogOpen"
    @close="dialogClose"
  >
    <div class="body-container table-container">
      <el-table :data="operationList" height="400">
        <el-table-column
          prop="operator"
          label="操作人"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="personType"
          label="人员类型"
          width="120px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="createdTime"
          label="时间"
          width="200px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="operateType"
          label="操作"
          width="120px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="operateContent"
          label="操作信息"
          width="200px"
          align="center"
        ></el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无操作记录" el-height="320px"></TableEmpty>
        </template>
      </el-table>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import { shallowRef } from "vue";

import { useModel } from "@assets/useHook";

import { operationRecordApi } from "@api/order-management-api";

const props = defineProps<{
  visible: boolean;
  orderInfo: Record<string, any>;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();
const visibleModel = useModel(props, "visible", emits);
const operationList = shallowRef<unknown[]>([]);
// 打开弹框
const dialogOpen = () => {
  getOperationRecord();
};
// 关闭弹框
const dialogClose = () => {
  visibleModel.value = false;
};
const getOperationRecord = async () => {
  try {
    const { data } = await operationRecordApi.getData(props.orderInfo.orderId);
    operationList.value = operationRecordApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
</script>
<style lang="scss" scoped>
.table-container .el-table {
  margin-top: 0;
}
</style>
